import uniIcon from '@/components/i-icon/i-icon.vue'
import { mapState } from 'vuex';
// import uniShader from '@/components/uni-shader.vue'
export default {
	components: {
		uniIcon
		// uniShader
	},
	props: {
		title: {
			type: String,
			default: '余额付款'
		},
		CouponPrice:{
			type:Number,
			default:0
		}
		
	},
	computed: mapState([ 'UserInfo']),
	data() {
		return {
			shader: false,
			password: [],
			items: [0, 1, 2, 3, 4, 5],
			pattern: 'hidden',
			patterns:'hidden'
		};
	},
	methods: {
		show() {
			this.shader = true;
			let args = {
				cancel: false
			};
			this.$emit('shown', args);
			if (!args.cancel) {
				this.pattern = 'slideup';
				this.patterns='blocks'
			}
		},
		hide() {
			this.shader = false;
			let args = {
				cancel: false
			};
			this.$emit('hidden', args);
			if (!args.cancel) {
				this.pattern = 'hidden';
				this.patterns='hidden'
				this.password=[]
			}
		},
		inputPwd(e) {
			let char = e.currentTarget.dataset.char;
			let args = {
				target: this,
				data: char,
				cancel: false,
				callback: undefined
			};
			this.$emit('input', args);
			if (!args.cancel){
				if (this.password.length === this.items.length) {
					console.log(this.password)
					return;
				}
				if (args.callback && typeof args.callback === 'function') {
					args.callback.call(this);
				}
				this.password.push(char);
				console.log(char)
				if (this.password.length === this.items.length) {
					setTimeout(()=>{
						//长度达6位，自动验证
						if(this.password.join('')==123456){
							this.$emit('completed', this.password.join(''));
							return;
						}
						this.$emit('completed','error');
						this.password=[]
						return;
					},200)
				}
			}
		},
		ClosePsd(){
			this.hide();
			this.password=[]
			uni.showToast({
				title: '您已取消支付',
				duration: 1000,
				icon:"none",
			});
		},
		backspace() {
			let args = {
				cancel: false
			};
			this.$emit('delete', args);
			if (!args.cancel) {
				if (this.password.length > 0) {
					this.password = this.password.slice(0, this.password.length - 1);
				}
			}
		}
	}
}
